// 
const baseSize = 10;
//  rem 
function setRem () {
    //  750 
    const scale = document.documentElement.clientWidth / 750;
    // 
    document.documentElement.style.fontSize = baseSize*scale + 'px'
}
// 
setRem();
//  rem
window.onresize = function () {
    setRem()
};



